<template>
    <div v-if="showConfirmDialog" class="confirm-dialog">
        <h5 class="confirmation-header">Confirm you want to return the bike to {{ stationName }} Station</h5>
        <button class="confirmation-btn margin-right" @click="cancelReturn()">Cancel</button>
        <button class="confirmation-btn" @click="returnBike()">Confirm</button>
    </div>
    <div v-else>
        <div class="station-row">
            <button v-for="(station, index) in stations" :key="station.id" class="station-btn" @click="returnBikeToStation(station.name)"><h4>{{ station.name }} Station</h4><p>( {{ numOfBikesInStations[index] }} / {{ station.total_stalls }})</p></button>
        </div>
    </div>
</template>

<script setup>
    import router from '@/router';
    import { ref, onMounted } from 'vue';
    import { defineProps } from 'vue';
    import {globalPostConnect} from '@/global.js';
    import {globalGetConnect} from '@/global.js';

    const props = defineProps({
        sessionId: String,
        bikeId: String
    });

    const stations = ref([]);
    const numOfBikesInStations = ref([]);

    async function getAllStations() {
      const baseUrl = "api/locations/stations"
      stations.value = (await globalGetConnect(baseUrl)).data
    }

    async function getNumberofBikesInEachStation() {
      const baseUrl = "api/locations/bikes/" + station.name + "/count"
      const response = (await globalGetConnect(baseUrl))
      numOfBikesInStations.value.push(response.data[0].string);
    }

    onMounted(async () => {
      await getAllStations();
      await getNumberofBikesInEachStation();
    });

const showConfirmDialog = ref(false);
const stationName = ref("");

function cancelReturn() {
    showConfirmDialog.value = false;
}

async function returnBike() {
    const baseUrl = "api/rentals/return/" + stationName.value + "/" + props.sessionId + "/" + props.bikeId
    (await globalPostConnect(baseUrl))

    router.push({
        path: '/'
    })
}

function returnBikeToStation(station) {
    stationName.value = station;
    showConfirmDialog.value = true;
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h5 {
    margin-top: 10px;
}
p {
  font-size: medium;
}
.station-row {
  margin-top: 30px;
}
.station-btn {
  width: 250px;
  border-radius: 10px;
  background-color: #a0c8d7;
  margin: 0px 20px;
  cursor: pointer;
}

.extend-btn {
    width: 420px;
    border-radius: 10px;
    background-color: #a0c8d7;
    margin: 0px 20px;
    cursor: pointer;
}

.confirm-dialog{
    border: 2px black solid;
    border-radius: 10px;
    margin-top: 30px;
    width: 50%;
    margin-left: 25%;
    margin-right: 25%;
}

.confirmation-btn {
  border-radius: 10px;
  background-color: #a0c8d7;
  border: 1px solid black;
  height: 30px;
  width: 100px;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 30px;
}

.confirmation-header {
    margin-top: 30px;
}

</style>