<template>
  <div class="session">
    <img alt="Vue logo" src="../assets/bike.png" />
    <h2>Session Id: {{ sessionId }}</h2>
    <h3>Start Time: {{ startTime }} &nbsp; Session Length: {{ sessionLength }}min</h3>
    <ReturnStationButtons :sessionId="sessionId" :bikeId="bikeId"></ReturnStationButtons>
    <div class="station-row">
      <button class="extend-btn" @click="extendSessionById()"><h5>Extend Session</h5></button>
    </div>
  </div>
</template>

<script setup>
import router from "@/router";
import ReturnStationButtons from "@/components/ReturnStationButtons.vue"
import { ref } from "vue";
import {globalPostConnect} from '@/global.js';

const sessionId = router.currentRoute.value.params.sessionId
const startTime = router.currentRoute.value.params.startTime
const sessionLength = ref(router.currentRoute.value.params.sessionLength)
const bikeId = router.currentRoute.value.params.bikeId

    async function extendSessionById() {
      const baseUrl = "api/rentals/extend/" + sessionId
      (await globalPostConnect(baseUrl))

      sessionLength.value = parseInt(sessionLength.value) + 15;
    }
</script>

<style scoped>

.station-row {
  margin-top: 30px;
}

.extend-btn {
    width: 420px;
    border-radius: 10px;
    background-color: #a0c8d7;
    margin: 0px 20px;
    cursor: pointer;
}

</style>